* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: 'montserrat', sans-serif;
  color: #fff;
  background-image: url(https://www.whatspaper.com/wp-content/uploads/2021/11/city-wallpaper-whatspaper-4.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
}
.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background: #0e0e0e83;
  height: 100vh;
}
.header {
  padding: 50px 20px 30px 20px;
}
.header h1 {
  font-size: 50px;
  color: #fbb034;
  text-shadow: 1px 3px rgba(0, 0, 0, 0.9);
}
.header input {
  width: 100%;
  padding: 10px;
  background: transparent;
  border: 0;
  outline: 0;
  border-bottom: 2px solid #fbb034;
  text-align: center;
  font-size: 20px;
  color: white;
}
.city {
  font-size: 35px;
  font-weight: 500;
  margin-bottom: 10px;
  text-shadow: 1px 5px rgba(0, 0, 0, 0.8);
}
.temp {
  font-size: 100px;
  font-weight: 700;
  margin-bottom: 15px;
  text-shadow: 1px 3px rgba(0, 0, 0, 0.1);
}
.desc {
  font-size: 25px;
  font-style: italic;
  text-shadow: 1px 3px rgba(0, 0, 0, 0.8);
  margin-bottom: 10px;
}
.minmax {
  font-size: 24px;
  text-shadow: 1px 3px rgba(0, 0, 0, 0.8);
}

